import { post, get } from './config'
import storage from '../common/storage'

export default {
	// 
	getLiveClass () {
		return get('api/live/live_class')
	},

	// 
	getLiveList (type,p) {
		return post(
			'api/live/anchor_list',
			{
				uid : storage.s_getItem("uid"),
        token : storage.s_getItem("token"),
        p,
        type
			}
		)
	},
	// /
	intoLiveRoom (live_id) {
		return post(
			'api/live/into_live',
			{
				uid : storage.s_getItem("uid"),
        token : storage.s_getItem("token"),
        live_id
			}
		)
	},
	// 
	outLiveRoom (live_id) {
		return post(
			'api/live/into_live',
			{
				uid : storage.s_getItem("uid"),
        token : storage.s_getItem("token"),
        live_id
			}
		)
	},
	// 
	getLiveInfo (live_id) {
		return post(
			'api/Live/live_info',
			{
				live_id
			}
		)
	},
	// 
	getAnchorInfo (live_id,user_id){
		return post(
			'api/live/get_live_info',
			{
				uid : storage.s_getItem("uid"),
        token : storage.s_getItem("token"),
				live_id,
				user_id
			}
		)
	},
	// 
	getRoomUser (live_id) {
		return post(
			'api/live/show_viewer',
			{
				uid : storage.s_getItem("uid"),
        token : storage.s_getItem("token"),
				page: 1,
        pagesize: 5,
				live_id
			}
		)
	},
	// 
	isFollow (user_id2,type) {
		return post(
			'api/live/follow',
			{
				uid : storage.s_getItem("uid"),
        token : storage.s_getItem("token"),
        user_id2,
        type
			}
		)
	},
	// 
	getLiveGoods (live_id) {
		return post(
			'api/merchant/live_goods',
			{
				uid : storage.s_getItem("uid"),
        token : storage.s_getItem("token"),
        live_id
			}
		)
	},
	// 
	getUserMoney () {
		return post(
			'api/live/get_money',
			{
				uid : storage.s_getItem("uid"),
        token : storage.s_getItem("token")
			}
		)
	},
	// 
	getGift () {
		return post(
			'api/live/gift_list',
			{
				uid : storage.s_getItem("uid"),
        token : storage.s_getItem("token")
			}
		)
	},
	// 
	getGiftCount (user_id) {
		return post(
			'api/live/get_get_money',
			{
				uid : storage.s_getItem("uid"),
        token : storage.s_getItem("token"),
        user_id
			}
		)
	},
	// 
	sendGift (data) {
		return post(
			'api/live/give_gift',
			{
				uid : storage.s_getItem("uid"),
        token : storage.s_getItem("token"),
        ...data
			}
		)
	}
}


