export default {
  props:{
    offset: {
      type: Number,
      default: 50 //
    },
    enableInfinite: { // 
      type: Boolean,
      default: true
    },
    enableRefresh: { // 
      type: Boolean,
      default: true
    },
    /**/
    onRefresh: {
      type: Function,
      default: undefined
    },
    /**/
    onInfinite: {
      type: Function,
      default: undefined
    }
  },
  data(){
    return {
      top: 0,
      state: 0,
      startX: 0,
      startY: 0,
      touching: false,
      hasNext: true, // 
      infiniteLoading: false,
      loadStart: false,
      loading: false //
    }
  },
  computed:{ // 
    showFooter(){
      return this.loadStart||this.loading||!this.hasNext
    }
  },
  created(){
    // this.infinite();
  },
  methods:{
    /*  */
    touchStart(e) {
      this.startY = e.targetTouches[0].pageY; // Y
      this.startX = e.targetTouches[0].pageX; // X
      this.startScroll = this.$el.scrollTop || 0; // $el,,0
      this.touching = true // 
    },
    /*  */
    touchMove(e) {
      if((!this.enableRefresh && !this.hasNext) || !this.touching) {
        return
      }
      let diff = e.targetTouches[0].pageY - this.startY - this.startScroll
      this.top = Math.pow(diff, 0.8) + (this.state === 2 ? this.offset : 0)
      if(this.state === 2) { // in refreshing
        return
      }
      if(diff > 0){ // 
        e.preventDefault(); // 
        console.log("")
        if(this.top >= this.offset) {
          this.state = 1
        }
      }else if(this.hasNext){ // 
        console.log("")
        this.state = 0;
        this.loadStart=true;
      }
      
    },
    /*  */
    touchEnd(e) { 
      this.touching = false;
      this.loadStart=false;
      if(this.state === 2) { // in refreshing
        this.state = 2
        this.top = this.offset
        return
      }
      if(this.enableRefresh) {
        if(this.top >= this.offset) { // do refresh
          this.refresh()
        } else { // cancel refresh
          this.state = 0
          this.top = 0
        }
      }

      // ----begin
      let endX = e.changedTouches[0].pageX,
          endY = e.changedTouches[0].pageY,
          dy = this.startY - endY,
          dx = endX - this.startX;
      //
      if(Math.abs(dx) < 2 && Math.abs(dy) < 2) {
        console.log("");
        return;
      }
      //--------end--------
      if(!this.enableInfinite || this.infiniteLoading) {
        return
      }
      let outerHeight = this.$el.clientHeight,
          innerHeight = this.$el.querySelector('.inner').clientHeight,
          scrollTop = this.$el.scrollTop,
          ptrHeight = this.onRefresh ? this.$el.querySelector('.pull-refresh').clientHeight : 0,
          bottom = innerHeight - outerHeight - scrollTop - ptrHeight;

      if(bottom <= this.offset && this.state === 0 && this.hasNext) {
        this.loading = true;
        this.infinite();
      } else {
        this.loading = false;
      }
    },
    /**/
    refresh() {
      if(this.onRefresh){
        this.state = 2;
        this.top = this.offset;
        setTimeout(() => {
          this.onRefresh(this.refreshDone) // 
        }, 300);
      }
    },
    /**/
    refreshDone() {
      this.state = 0
      this.top = 0
    },
    /*  */
    infinite() { 
      if(this.onInfinite){
        this.infiniteLoading = true;
        this.loading = true;
        setTimeout(() => {
          this.onInfinite(this.infiniteDone) // 
        },300)
      }
    },
    /*  */
    infiniteDone(bool) {
      this.infiniteLoading = false;
      this.loading=false;
      this.hasNext=bool;
    }
  }
}



