// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import VueLazyload from 'vue-lazyload'
import util from './common/util'
import storage from './common/storage'

import errorImg from './assets/icon/error.png'
import loadingImg from './assets/icon/loading.png'

Vue.config.productionTip = false

// 
Vue.use(util);

// 
Vue.use(VueLazyload, {
  error: errorImg, // 
  loading: loadingImg // 
})

// 
Vue.prototype.goBeforeLoginUrl = () => {
  let url = storage.s_getItem('beforeLoginUrl')
  if(!url || url.indexOf('/author') != -1){
    router.push('/home')
  }else{
    if(url == '/'){
      url = '/home'
    }
    router.push(url)
    storage.s_setItem('beforeLoginUrl', '')
  }
}

// 
Vue.loginInvalid  = () => {
  storage.s_clear() // sessionStorage
  storage.s_setItem('beforeLoginUrl',router.history.current.path) // url
  router.push('/author')
  return false
}

/*
* 
* urlrouter.beforeEach
*/
router.beforeEach((to, from, next) => {
  if(to.path == '/author' && storage.s_getItem('uid') && storage.s_getItem('token')){
    // 
    next('/home')
    return false
  }
  /*
  * sessionStorage
  * 
  */
  if(!storage.s_getItem('uid') && !storage.s_getItem('token') && to.path !='/author'){
    storage.s_setItem('beforeLoginUrl', to.fullPath) // url
    next('/author') // 
    return false
  }else {
    next()
  }
})

// 
Array.prototype.contains = function(obj) {
  console.log(this);
  for (var i = 0; i < this.length + 1; i++) {
    if (this[i] == obj) {
      return true;
    }
  }
  return false;
}

/** 
*  
**/ 
Array.prototype.remove=function(obj){ 
  for(var i =0;i <this.length;i++){ 
    var temp = this[i]; 
    if(!isNaN(obj)){ 
    temp=i; 
  } 
  if(temp == obj){ 
    for(var j = i;j <this.length;j++){ 
      this[j]=this[j+1]; 
    } 
    this.length = this.length-1; 
    } 
  } 
}


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



