import api from '../../../api/api'
import MyBanner from '../../../components/banner/banner'
import GShop from './private/shop' 
import GoodsSpec from './private/goodsSpec'

export default {
	data() {
		return {
			goodsDetails: {}, // 
			shopDetails: {},  // 
			specState: false,  // 
			specType: null,   // 'car''pay'
		}
	},
  methods: {
  	// 
  	_getGoodsDetailsFun() {
  		this.setLoading(true)
  		api.getGoodsDetailsFun(this.$route.query.goods_id)
  		.then((res) => {
  			this.setLoading(false)
        if(res.status ==='ok'){
          this.goodsDetails = res.data
          this._getShopDetailsFun(res.data.merchants_id)
        }else if(res.status === 'error'){
          console.log(res.data)
        }
      })
  	},

  	// 
  	_getShopDetailsFun(id) {
  		api.getShopDetailsFun(id)
  		.then((res) => {
  			if(res.status === 'ok'){
          this.shopDetails = res.data
        }else if(res.status === 'error'){
          console.log(res.data)
        }
  		})
  	},
  	// model
  	specModelToggle(state,type) {
  		if(state == 0){ // 
  			this.specType = null
  			this.specState = false
  		}else {  // 
  			this.specType = type
  			this.specState = true
  		}
  	},
  	
  	//
  	collectionClick () {
  		api.collectionFun(this.$route.query.goods_id)
  		.then((res) => {
  			if(res.status === 'ok'){
  				this.goodsDetails.is_collect = res.data
  				if(res.data == 1) {
  					this.promptFun({
  						content: '',
  						type: 'success'
  					})
  				}else{
  					this.promptFun({
  						content: '',
  						type: 'success'
  					})
  				}
  			}else if(res.status === 'error'){
  				this.promptFun({
						content: res.data,
						type: 'error'
					})
  			}
  		})
  	}
  },
  created () {
  	this._getGoodsDetailsFun()
  },
  components: {
  	MyBanner,
  	GShop,
  	GoodsSpec
  }
}



